import utlis from '../../static/js/utlis.js'
export default {
	data() {
		return {
			version: '',
			content: ''
		}
	},
	onShow() {
		this.getVersion();
	},
	methods: {
		//前往其他小程序
		// toOtherApplets() {
		// 	uni.navigateToMiniProgram({
		// 		appId: 'wxd5b706797f401a0d',
