		// 		success(res) {
		// 			// 打开成功
		// 			console.log('success')
		// 		}
		// 	})
		// },
		// 获取版本信息
		getVersion() {
			let self = this;
			let data = {
				userSource: 1,
				xmType: 1
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			utlis.http("POST", 'cdxmVersion', data, function(res) {
				if (res.status == "success") {
					self.version = res.version;
					self.content = res.content;
					uni.setStorageSync('version',res.version)
					uni.hideLoading()
				} else {
					uni.hideLoading()
				}
			})
		}
	}
}
